 -->
<h3>Input 单行文本输入框</h3>
<hr>
<h4>使用方式</h4>
<pre v-pre><code v-pre class="language-javascript">
<span class="hljs-keyword">import</span> Vue <span class="hljs-keyword">from</span> <span class="hljs-string">'vue'</span>
<span class="hljs-keyword">import</span> { Input } <span class="hljs-keyword">from</span> <span class="hljs-string">'vvmui'</span>

Vue.component(Input)

</code></pre>
<h4>基础用法</h4>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">Input</span> /&gt;</span>

</code></pre>
<h4>使用icon</h4>
<p>通过使用<code v-pre>icon</code>属性显示左边icon</p>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">Input</span> <span class="hljs-attr">icon</span>=<span class="hljs-string">"iconaccount"</span> /&gt;</span>

</code></pre>
<h4>设置右边展示icon</h4>
<p>通过使用<code v-pre>right-icon</code>属性显示右边icon</p>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">Input</span> <span class="hljs-attr">@on-click</span>=<span class="hljs-string">"click"</span> <span class="hljs-attr">right-icon</span>=<span class="hljs-string">"iconinfo"</span> /&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  <span class="hljs-attr">methods</span>: {
    click() {
      <span class="hljs-keyword">this</span>.$Toast.info(<span class="hljs-string">"消息"</span>);
    }
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
<h4>展示单位</h4>
<p>通过使用<code v-pre>unity</code>属性可设置单位</p>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">Input</span> <span class="hljs-attr">unity</span>=<span class="hljs-string">"元"</span> /&gt;</span>

</code></pre>
<h4>禁用</h4>
<p>通过使用<code v-pre>disabled</code>属性可禁用输入框</p>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">Input</span> <span class="hljs-attr">disabled</span> <span class="hljs-attr">unity</span>=<span class="hljs-string">"元"</span> /&gt;</span>

</code></pre>
<h3>API</h3>
<h5>Input props</h5>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>icon</td>
<td>左边icon图标</td>
<td>String</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>v-model</td>
<td>绑定值</td>
<td>String/Number</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>placeholder</td>
<td>占位文本</td>
<td>String</td>
<td>无</td>
<td>请输入内容</td>
</tr>
<tr>
<td>right-icon</td>
<td>右边icon</td>
<td>String</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用输入框</td>
<td>Boolean</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>unity</td>
<td>单位属性，传入会展示在右边</td>
<td>String</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>maxlength</td>
<td>最大输入长度</td>
<td>Number</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>type</td>
<td>input原生type属性</td>
<td>String</td>
<td><code v-pre>text</code> <code v-pre>password</code></td>
<td>text</td>
</tr>
<tr>
<td>scroll-target</td>
<td>使用scroll的dom节点名称，<code v-pre>ios系统键盘关闭不重绘页面scroll的可使用</code></td>
<td>String</td>
<td>无</td>
<td>无</td>
</tr>
</tbody>
</table>
<h5>Input Events</h5>
<table>
<thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-click</td>
<td>点击右边icon触发</td>
<td>无</td>
</tr>
</tbody>
</table>
<h5>更新日志</h5>
<table>
<thead>
<tr>
<th style="text-align:center">版本</th>
<th style="text-align:left">类型</th>
<th style="text-align:left">内容</th>
</tr>
</thead>
<tbody></tbody>
</table>
</section></template>

<script>
export default {
  methods: {
    click() {
      this.$Toast.info("消息");
    }
  }
};
</script>


